export default class Song {
  constructor (option) {
    this.id = option.id
    this.mid = option.mid
    this.singer = option.singer
    this.name = option.name
    this.album = option.album
    this.duration = option.duration
    this.image = option.image
    this.url = option.url
  }
}

export function createSong (musicData) {
  return new Song({
    id: musicData.songid,
    mid: musicData.songmid,
    singer: fillterSinger(musicData.singer),
    name: musicData.songname,
    album: musicData.albumname,
    duration: musicData.interval,
    image: `//y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`,
    url: `http://ws.stream.qqmusic.qq.com/${musicData.songid}.m4a?fromtag=46`
  })
}

function fillterSinger (singer) {
  var ret = []
  singer.forEach((item) => {
    ret.push(item.name)
  })
  return ret.join('/')
}



