<template>
	<div class="man">
		<div class="top">
			<img src="http://m.fubaokj.com/css/img/logo.png" alt="">
			<div class="small">
				<p>10</p>
				<p>3</p>
			</div>
		</div>
		<div class="ad">
			<img src="http://test.fubaokj.com/img/web/man.jpg" alt="">
		</div>
		<div class="content">
			<h3 class="title"> </h3>
			<div class="content-box">
				<p> 771</p>
				<p> 622</p>
				<p> 7722</p>
			</div>
		</div>
		<div class="btn" @click="getMan()">
			
		</div>
  	<router-view></router-view>
	</div>
</template>
<script type="text/ecmascript-6">
	export default {
	  methods: {
	    getMan () {
	      this.$router.push({
	        path: '/man/detail'
	      })
	    }
	  }
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .man
  	background-color: #fff
  	.top
  		height: 60px
  		padding: 14px 10px
  		box-sizing: border-box
  		display: flex
  		justify-content: space-between
  		img, .small
  			height: 100%
  		.small
  			p
  				font-size: 12px
  				color: #333
  				line-height: 1
  				text-align: right
  				text-overflow: ellipsis
  				overflow: hidden
  				white-space: nowrap
  				&+p
  					margin-top: 8px
  					letter-spacing: .5px
		.ad
  		img
  			width: 100%
  	.content
  		text-align: center
  		padding-top: 50px
  		.title
  			color: #f46564
  			font-size: 20px
  			margin: 0
  			padding: 0
  		.content-box
  			padding: 20px 10px
  			p
  			 color: #666
  			 font-size: 14px
  			 line-height: 1.8em
  			 text-align: left
  	.btn
  		text-align: center
  		background: #42ad45
  		font-size: 20px
  		background: linear-gradient(to right,#02d56e,#42ad45)
  		border: 1px solid #56be7d
  		box-sizing: border-box
  		width: 92%
  		border-radius: 5px
  		margin: auto
  		line-height: 50px
  		color: #fff
  		margin-bottom: 5px
</style>



